'use client'

import { useState, useEffect, useRef } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Progress } from '@/components/ui/progress'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Alert, AlertDescription } from '@/components/ui/alert'

const motivationalQuotes = [
  "Take time to make your soul happy.",
  "Breathe in peace, breathe out stress.",
  "You are stronger than you think.",
  "This too shall pass.",
  "Progress, not perfection.",
  "One step at a time.",
  "You've got this.",
  "Calm mind brings inner strength.",
  "Focus on what you can control.",
  "Be kind to yourself today.",
  "Every breath is a new beginning.",
  "Stress is temporary, but your strength is permanent.",
  "Take a deep breath and let it go.",
  "You are capable of amazing things.",
  "Peace begins with a smile."
]

export default function TipsPage() {
  const [currentQuote, setCurrentQuote] = useState('')
  const [breathingActive, setBreathingActive] = useState(false)
  const [breathingPhase, setBreathingPhase] = useState<'inhale' | 'hold' | 'exhale'>('inhale')
  const [breathingProgress, setBreathingProgress] = useState(0)
  const [breathingCycle, setBreathingCycle] = useState(0)
  const [timerMinutes, setTimerMinutes] = useState(5)
  const [timerActive, setTimerActive] = useState(false)
  const [timeRemaining, setTimeRemaining] = useState(0)
  const [musicPlaying, setMusicPlaying] = useState(false)
  
  const audioRef = useRef<HTMLAudioElement>(null)
  const breathingIntervalRef = useRef<NodeJS.Timeout | null>(null)
  const timerIntervalRef = useRef<NodeJS.Timeout | null>(null)

  useEffect(() => {
    // Set initial quote
    setCurrentQuote(motivationalQuotes[Math.floor(Math.random() * motivationalQuotes.length)])
  }, [])

  useEffect(() => {
    return () => {
      // Cleanup intervals on unmount
      if (breathingIntervalRef.current) clearInterval(breathingIntervalRef.current)
      if (timerIntervalRef.current) clearInterval(timerIntervalRef.current)
    }
  }, [])

  const getNewQuote = () => {
    const newQuote = motivationalQuotes[Math.floor(Math.random() * motivationalQuotes.length)]
    setCurrentQuote(newQuote)
  }

  const startBreathingExercise = () => {
    setBreathingActive(true)
    setBreathingCycle(0)
    setBreathingPhase('inhale')
    setBreathingProgress(0)

    let phase: 'inhale' | 'hold' | 'exhale' = 'inhale'
    let progress = 0
    let cycle = 0

    breathingIntervalRef.current = setInterval(() => {
      progress += 2

      if (phase === 'inhale' && progress >= 100) {
        phase = 'hold'
        progress = 0
      } else if (phase === 'hold' && progress >= 100) {
        phase = 'exhale'
        progress = 0
      } else if (phase === 'exhale' && progress >= 100) {
        phase = 'inhale'
        progress = 0
        cycle += 1
      }

      setBreathingPhase(phase)
      setBreathingProgress(progress)
      setBreathingCycle(cycle)

      if (cycle >= 10) { // Complete after 10 cycles
        setBreathingActive(false)
        if (breathingIntervalRef.current) clearInterval(breathingIntervalRef.current)
      }
    }, 100) // Update every 100ms for smooth animation
  }

  const stopBreathingExercise = () => {
    setBreathingActive(false)
    if (breathingIntervalRef.current) clearInterval(breathingIntervalRef.current)
  }

  const startTimer = () => {
    const totalSeconds = timerMinutes * 60
    setTimeRemaining(totalSeconds)
    setTimerActive(true)

    timerIntervalRef.current = setInterval(() => {
      setTimeRemaining(prev => {
        if (prev <= 1) {
          setTimerActive(false)
          if (timerIntervalRef.current) clearInterval(timerIntervalRef.current)
          
          // Show notification if permission granted
          if ('Notification' in window && Notification.permission === 'granted') {
            new Notification('Timer Complete! 🔔', {
              body: 'Your relaxation timer has finished. Take a moment to check in with yourself.'
            })
          }
          
          return 0
        }
        return prev - 1
      })
    }, 1000)
  }

  const stopTimer = () => {
    setTimerActive(false)
    setTimeRemaining(0)
    if (timerIntervalRef.current) clearInterval(timerIntervalRef.current)
  }

  const toggleMusic = () => {
    if (audioRef.current) {
      if (musicPlaying) {
        audioRef.current.pause()
      } else {
        audioRef.current.play().catch(() => {
          // Handle play error silently
        })
      }
      setMusicPlaying(!musicPlaying)
    }
  }

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins}:${secs.toString().padStart(2, '0')}`
  }

  const getBreathingInstruction = () => {
    switch (breathingPhase) {
      case 'inhale':
        return 'Breathe in slowly...'
      case 'hold':
        return 'Hold your breath...'
      case 'exhale':
        return 'Breathe out slowly...'
    }
  }

  return (
    <div className="space-y-6 pb-20">
      <div className="text-center space-y-2">
        <h1 className="text-3xl font-bold tracking-tight">Stress Relief Tools</h1>
        <p className="text-muted-foreground">
          Techniques and tools to help you manage stress and find calm
        </p>
      </div>

      {/* Motivational Quote */}
      <Card>
        <CardHeader>
          <CardTitle>Daily Motivation</CardTitle>
          <CardDescription>Positive thoughts for your wellness journey</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="text-center space-y-4">
            <blockquote className="text-lg font-medium italic text-primary">
              "{currentQuote}"
            </blockquote>
            <Button onClick={getNewQuote} variant="outline">
              New Quote
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Breathing Exercise */}
      <Card>
        <CardHeader>
          <CardTitle>Breathing Exercise</CardTitle>
          <CardDescription>4-4-4 breathing technique for instant calm</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {breathingActive ? (
              <div className="text-center space-y-4">
                <div className="text-2xl font-bold text-primary">
                  Cycle {breathingCycle + 1} of 10
                </div>
                <div className="text-lg font-medium">
                  {getBreathingInstruction()}
                </div>
                <div className="space-y-2">
                  <Progress value={breathingProgress} className="h-4" />
                  <div className="text-sm text-muted-foreground">
                    Phase: {breathingPhase.charAt(0).toUpperCase() + breathingPhase.slice(1)}
                  </div>
                </div>
                <Button onClick={stopBreathingExercise} variant="outline">
                  Stop Exercise
                </Button>
              </div>
            ) : (
              <div className="text-center space-y-4">
                <div className="space-y-2">
                  <p className="text-sm text-muted-foreground">
                    Inhale for 4 seconds, hold for 4 seconds, exhale for 4 seconds
                  </p>
                  <p className="text-sm text-muted-foreground">
                    Complete 10 cycles for maximum benefit
                  </p>
                </div>
                <Button onClick={startBreathingExercise} className="w-full">
                  Start Breathing Exercise
                </Button>
              </div>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Relaxation Timer */}
      <Card>
        <CardHeader>
          <CardTitle>Relaxation Timer</CardTitle>
          <CardDescription>Set a timer for meditation or quiet time</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {!timerActive ? (
              <div className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="timer-minutes">Duration (minutes)</Label>
                  <Input
                    id="timer-minutes"
                    type="number"
                    min="1"
                    max="60"
                    value={timerMinutes}
                    onChange={(e) => setTimerMinutes(parseInt(e.target.value) || 5)}
                  />
                </div>
                <Button onClick={startTimer} className="w-full">
                  Start Timer ({timerMinutes} min)
                </Button>
              </div>
            ) : (
              <div className="text-center space-y-4">
                <div className="text-4xl font-bold text-primary">
                  {formatTime(timeRemaining)}
                </div>
                <div className="text-sm text-muted-foreground">
                  Time remaining
                </div>
                <Button onClick={stopTimer} variant="outline">
                  Stop Timer
                </Button>
              </div>
            )}
          </div>
        </CardContent>
      </Card>

      {/* Calming Sounds */}
      <Card>
        <CardHeader>
          <CardTitle>Calming Sounds</CardTitle>
          <CardDescription>Peaceful audio to help you relax</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div className="text-center space-y-4">
              <div className="text-sm text-muted-foreground">
                Nature sounds for relaxation
              </div>
              <Button 
                onClick={toggleMusic} 
                variant={musicPlaying ? "default" : "outline"}
                className="w-full"
              >
                {musicPlaying ? 'Pause Sounds' : 'Play Calming Sounds'}
              </Button>
              <audio
                ref={audioRef}
                loop
                onPlay={() => setMusicPlaying(true)}
                onPause={() => setMusicPlaying(false)}
                onEnded={() => setMusicPlaying(false)}
                className="hidden"
              >
                <source src="/calm-nature.mp3" type="audio/mpeg" />
                Your browser does not support the audio element.
              </audio>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Stress Management Tips */}
      <Card>
        <CardHeader>
          <CardTitle>Stress Management Tips</CardTitle>
          <CardDescription>Evidence-based techniques for reducing stress</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid gap-4 md:grid-cols-2">
            <div className="space-y-2">
              <h4 className="font-medium">Quick Relief Techniques:</h4>
              <ul className="text-sm text-muted-foreground space-y-1 ml-4">
                <li>• Deep breathing exercises</li>
                <li>• Progressive muscle relaxation</li>
                <li>• Mindful meditation (5-10 minutes)</li>
                <li>• Take a short walk outside</li>
                <li>• Listen to calming music</li>
              </ul>
            </div>
            
            <div className="space-y-2">
              <h4 className="font-medium">Long-term Strategies:</h4>
              <ul className="text-sm text-muted-foreground space-y-1 ml-4">
                <li>• Regular exercise routine</li>
                <li>• Consistent sleep schedule</li>
                <li>• Healthy eating habits</li>
                <li>• Social connections</li>
                <li>• Time management skills</li>
              </ul>
            </div>
          </div>
          
          <Alert>
            <AlertDescription>
              Remember: If stress becomes overwhelming or persistent, consider speaking with a healthcare professional or counselor.
            </AlertDescription>
          </Alert>
        </CardContent>
      </Card>

      {/* Emergency Stress Relief */}
      <Card>
        <CardHeader>
          <CardTitle>Emergency Stress Relief</CardTitle>
          <CardDescription>When you need immediate calm</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-3">
            <div className="p-4 bg-blue-50 dark:bg-blue-950 rounded-lg">
              <h4 className="font-medium text-blue-900 dark:text-blue-100 mb-2">
                5-4-3-2-1 Grounding Technique
              </h4>
              <div className="text-sm text-blue-800 dark:text-blue-200 space-y-1">
                <p>• 5 things you can see</p>
                <p>• 4 things you can touch</p>
                <p>• 3 things you can hear</p>
                <p>• 2 things you can smell</p>
                <p>• 1 thing you can taste</p>
              </div>
            </div>
            
            <div className="p-4 bg-green-50 dark:bg-green-950 rounded-lg">
              <h4 className="font-medium text-green-900 dark:text-green-100 mb-2">
                Box Breathing
              </h4>
              <div className="text-sm text-green-800 dark:text-green-200">
                <p>Inhale for 4 counts → Hold for 4 counts → Exhale for 4 counts → Hold for 4 counts</p>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
